$margin-element: 10px;

.App {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;

  .pointList {
    display: flex;
    flex-direction: column;
    flex: 1;

    .point {
      padding: 15px;
      margin: $margin-element;
      background: white;

      .point-address {
        margin: 0 0 20px 0;
      }

      .point-budgets {
        display: flex;

        .point-budgets-budget {
          margin: 0 10px 0 0;
          padding: 5px 10px;
          background: grey;
        }
      }
    }
  }

  .map {
    flex: 1;
    height: 80vh;
    margin: $margin-element;
  }
}











